import { useSpring, animated } from "@react-spring/web";
import { useEffect, useRef } from "react";

const usePrevious = (value) => {
  const ref = useRef(value);
  useEffect(() => {
    ref.current = value;
  }, [value]);
  return ref.current;
};

const AnimatedNumber = ({ number, Tag = "span" }) => {
  const previousNumber = usePrevious(number); // 

  const props = useSpring({
    from: { number: previousNumber || 0 }, // 
    to: { number: number }, // 
    config: { duration: 500 }, // 1 
  });

  const AnimatedTag = animated[Tag];

  return (
    <AnimatedTag>{props.number.to((n) => `$${Math.floor(n)}`)}</AnimatedTag>
  );
};

export default AnimatedNumber;
