import React, { useContext } from "react";
import "./Acc.css";

import { UserContext } from "../context/userContext";
function Acc() {
  const {
    loginUser,
    updateLoginInfo,
    loginInfo,
    siginUser,
    updateSiginInfo,
    siginInfo,

    message,
  } = useContext(UserContext);

  const change = (moment) => {
    if (moment === "login") {
      document.getElementsByClassName("login")[0].style.display = "flex";
      document.getElementsByClassName("sign")[0].style.display = "none";
    } else {
      document.getElementsByClassName("login")[0].style.display = "none";
      document.getElementsByClassName("sign")[0].style.display = "flex";
    }
  };

  return (
    <>
      <div className="acc">
        <form
          onSubmit={(e) => {
            e.preventDefault();
            loginUser();
          }}
          className="login"
        >
          <h3></h3>
          <div className="user">
            <label htmlFor="log-name"> </label>
            <input
              type="email"
              id="log-name"
              value={loginInfo.email}
              onChange={(e) =>
                updateLoginInfo({ ...loginInfo, email: e.target.value })
              }
              placeholder=""
            />
          </div>
          <div className="user">
            <label htmlFor="log-pass"> </label>
            <input
              type="password"
              id="log-pass"
              value={loginInfo?.password}
              onChange={(e) =>
                updateLoginInfo({ ...loginInfo, password: e.target.value })
              }
              placeholder=""
            />
          </div>
          <div style={{ display: "flex" }}>
            <p>?</p>
            <p
              style={{ fontWeight: "600", cursor: "default" }}
              onClick={() => change("sign")}
            >
              
            </p>
          </div>

          <button
            disabled={message.text === "..."}
            onClick={() => loginUser()}
          >
            {message.text === "..." ? "..." : ""}
          </button>
        </form>
        <form
          onSubmit={(e) => {
            e.preventDefault();
            siginUser();
          }}
          className="sign"
        >
          <h3></h3>

          <div className="user">
            <label htmlFor="sig-name"> </label>
            <input
              type="email"
              id="sig-name"
              onChange={(e) =>
                updateSiginInfo({ ...siginInfo, email: e.target.value })
              }
              placeholder=""
            />
          </div>
          <div className="user">
            <label htmlFor="sig-name"> </label>
            <input
              type="text"
              id="sig-name"
              onChange={(e) =>
                updateSiginInfo({ ...siginInfo, name: e.target.value })
              }
              placeholder=""
            />
          </div>
          <div className="user">
            <label htmlFor="sig-pass"> </label>
            <input
              type="password"
              id="sig-pass"
              onChange={(e) =>
                updateSiginInfo({ ...siginInfo, password: e.target.value })
              }
              placeholder=""
            />
          </div>
          <div style={{ display: "flex" }}>
            <p>?</p>
            <p
              style={{ fontWeight: "600", cursor: "default" }}
              onClick={() => change("login")}
            >
              
            </p>
          </div>

          <button disabled={message.text === "..."} type="submit">
            {message.text === "..." ? "..." : ""}
          </button>
        </form>
      </div>
    </>
  );
}

export default Acc;
