import React, { useContext, useEffect, useState } from "react";
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";
import { AccountContext } from "../context/accountContext";
import DashRecord from "./DashRecord";
import { Helmet } from "react-helmet-async";
import moment from "moment";
import { driver } from "driver.js";
import "driver.js/dist/driver.css";
import "./Dash.css";
import { UserContext } from "../context/userContext";

function Dash() {
  const {
    setSelectedDate,
    selectedDate,
    records,
    setCurrentMonth,
    currentMonth,
  } = useContext(AccountContext);
  const { driverStep, setDriverStep } = useContext(UserContext);
  const [events, setEvents] = useState([]);
  useEffect(() => {
    if (driverStep.some((item) => item === "dash")) return;
    const driverObj = driver({
      nextBtnText: "",
      prevBtnText: "",
      doneBtnText: "!",
      steps: [
        {
          element: ".calendar",
          popover: {
            title: "",
            description: "",
          },
        },
        {
          element: ".records",
          popover: {
            title: "",
            description: " ",
          },
        },
        {
          element: ".nav",
          popover: {
            title: "",
            description: "",
          },
        },
        {
          element: ".nav li:nth-child(1) a",
          popover: {
            title: "",
            description: " ",
          },
        },
        {
          element: ".nav li:nth-child(2) a",
          popover: {
            title: "",
            description: "",
          },
        },
        {
          element: ".nav li:nth-child(3) button",
          popover: {
            title: "",
            description:
              " ...",
          },
        },
        {
          element: ".nav li:nth-child(4) a",
          popover: {
            title: "",
            description: " ",
          },
        },
        {
          element: ".nav li:nth-child(5) a",
          popover: {
            title: "",
            description: "",
          },
        },
      ],
    });

    driverObj.drive();
    setDriverStep((prev) => [...prev, "dash"]);
  }, [driverStep, setDriverStep]);
  useEffect(() => {
    if (!records) return;

    let groupedData = {}; // 

    //  source 
    records.forEach((record) => {
      const key = `${moment(record.date).format("YYYY-MM-DD")}_${
        record.source
      }`; // 
      if (!groupedData[key]) {
        groupedData[key] = {
          count: 0,
          totalAmount: 0,
          date: record.date,
          source: record.source,
        };
      }
      groupedData[key].count++;
      groupedData[key].totalAmount += record.amount;
    });
    let eventsData = Object.values(groupedData).map((group, index) => ({
      id: index + 1,
      title: `${group.count > 1 ? `${group.count}` : "1"}$${
        group.totalAmount
      }`,
      source: group.source,
      start: group.date,
      allDay: true,
      color:
        group.source === "income"
          ? "#388E3C"
          : group.source === "expense"
          ? "#D32F2F"
          : "#1976D2",
    }));

    setEvents(eventsData);
  }, [records]);
  useEffect(() => {
    const abbr = document.querySelectorAll("abbr[aria-label]");
    abbr.forEach((el) => {
      if (el.getAttribute("aria-label") === moment().format("YYYYMD")) {
        el.textContent = "";
      }
    });
  }, [selectedDate, currentMonth]);

  return (
    <div className="dash">
      <Helmet>
        <title></title>
      </Helmet>

      <div className="calendar">
        <Calendar
          defaultValue={selectedDate}
          value={selectedDate}
          onChange={(e) => {
            setSelectedDate(e);
          }}
          onActiveStartDateChange={(e) => {
            setSelectedDate(moment(e.activeStartDate).format("YYYY-MM-DD"));
            setCurrentMonth(moment(e.activeStartDate).format("YYYY-MM"));
          }}
          navigationAriaLive="off"
          prev2Label={null}
          next2Label={null}
          onClickDay={(e) => setCurrentMonth(moment(e).format("YYYY-MM"))}
          onClickMonth={(e) => setCurrentMonth(moment(e).format("YYYY-MM"))}
          tileContent={({ date }) => {
            return (
              <>
                <div
                  style={{
                    position: "relative",
                  }}
                >
                  <div
                    style={{
                      position: "absolute",
                      width: "100%",
                      display: "flex",
                      justifyContent: "center",
                      gap: "5px",
                      zIndex: "1",
                    }}
                  >
                    {events.find(
                      (item) =>
                        moment(item.start).format("YYYY-MM-DD") ===
                          moment(date).format("YYYY-MM-DD") &&
                        item.source === "expense"
                    ) && (
                      <div
                        style={{
                          padding: "2.5px",
                          backgroundColor: "#D32F2F",
                          borderRadius: "50px",
                        }}
                      />
                    )}
                    {events.find(
                      (item) =>
                        moment(item.start).format("YYYY-MM-DD") ===
                          moment(date).format("YYYY-MM-DD") &&
                        item.source === "income"
                    ) && (
                      <div
                        style={{
                          padding: "2.5px",
                          backgroundColor: "rgb(56, 205, 9)",
                          borderRadius: "50px",
                        }}
                      />
                    )}
                    {events.find(
                      (item) =>
                        moment(item.start).format("YYYY-MM-DD") ===
                          moment(date).format("YYYY-MM-DD") &&
                        item.source === "change"
                    ) && (
                      <div
                        style={{
                          padding: "2.5px",
                          backgroundColor: "#1976D2",
                          borderRadius: "50%",
                        }}
                      />
                    )}
                  </div>
                </div>
              </>
            );
          }}
        />
      </div>
      <DashRecord />
    </div>
  );
}

export default Dash;
